*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
}
.App{
  position: relative;
}
main{
  display: grid;
  grid: 1fr / 1fr 1fr;
  place-items: center;
  gap: 30px;
  width: calc(87% + 30px);;
  margin: auto;
  padding: 50px 0;
}
.cards_con{
  width: 90%;
  height: 509px;
  display: grid;
  grid: repeat(3,1fr) / 1fr;
  align-self: stretch;
  gap: 20px;
}
.green, .red{
  font-weight: bold;
}
.green{
  color: green;
}

.red{
  color: red;
}

@media (min-width: 320px) and (max-width:1024px){
  .App{
    display: flex;
    flex-direction: column;

  }
  main{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
}